KQ,iCAAgC,EACnC;;AJtqDL;EACI,gBAAe,EAClB;;AAED;EACI,YAAW;EACX,4BAA2B;EAC3B,kBAAiB,EACpB","file":"client.scss","sourcesContent":["/*! -----------------------------------------------------------------\n[Master Stylesheet for Client]\n\nProject:     Ajaxtown\nVersion:     1.0\nLast change: 21/03/2017\nPrimary use: Blog\n\n------------------------------------------------------------------\n[Sidebar Styles]\n*/\n\n@import \"./common.scss\";\nh3.widget-title {\n    font-size: 14px;\n}\n\nh3.widget-title:before {\n    content: \"\";\n    border-left: 2px solid #333;\n    margin-right: 5px;\n}",":root {\n    --color-text-primary: #333333;\n    --color-text-primary-invert: #b7b7b7;\n    --color-text-primary-light: #616161;\n    --color-text-muted: rgb(185, 185, 185);\n    --color-text-secondary: #000000;\n    --color-text-secondary-light: #4c4c4c;\n    --color-accent: #2196f3;\n    --color-bg-primary: #f1f1f1;\n    --color-bg-secondary: #1f1e1e;\n    --color-border: #e5e5e5;\n    --link-hover: #1a82d6;\n    --color-menu-link: var(--color-text-primary-invert);\n}\n\n$racing_font: \"Racing Sans One\",\ncursive;\n\n/*!------------------------------------------------------------------\n[Body Color]\n*/\n\n$color-text-primary: var(--color-text-primary);\n\n/*!------------------------------------------------------------------\n[Headlines]\n*/\n\n$color-text-secondary: var(--color-text-secondary);\n\n/*!------------------------------------------------------------------\n[Help text]\n*/\n\n$color-text-primary-light: var(--color-text-primary-light);\n\n/*!------------------------------------------------------------------\n[Placeholders]\n*/\n\n$color-text-muted: var(--color-text-muted);\n\n/*!------------------------------------------------------------------\n[Contrast for backgrounds. This should be opposite of text-primary-color]\n*/\n\n$color-text-primary-invert: var(--color-text-primary-invert);\n\n/*!------------------------------------------------------------------\n[Input boxes, text areas, etc. All .form-control]\n*/\n\n$color-text-secondary-light: var(--color-text-secondary-light);\n\n/*!------------------------------------------------------------------\n[Something to contrast]\n*/\n\n$color-accent: var(--color-accent);\n\n/*!------------------------------------------------------------------\n[Body background color]\n*/\n\n$color-bg-primary: var(--color-bg-primary);\n\n/*!------------------------------------------------------------------\n[Sidebar color]\n*/\n\n$color-bg-secondary: var(--color-bg-secondary);\n\n/*!------------------------------------------------------------------\n[Link colors]\n*/\n\n$link_active: var(--color-accent);\n$link_hover: var(--link-hover);\n\n/*!------------------------------------------------------------------\n[Borders]\n*/\n\n$color-border: var(--color-border);\n\n/*!------------------------------------------------------------------\n[Menu Link Color]\n*/\n\n$color-menu-link: var(--color-menu-link);",".wrapper {\n    position: relative;\n    margin-left: 250px;\n}",".sidebar {\n    position: fixed;\n    width: 250px;\n    background-color: $color-bg-secondary;\n    height: 100%;\n    top: 0;\n    left: 0;\n    z-index: 999;\n    background-repeat: no-repeat;\n    background-position: center center;\n    background-size: cover;\n    a {\n        color: rgba(255, 255, 255, 0.7);\n        &:hover {\n            color: #fff;\n        }\n    }\n    .copyright {\n        position: absolute;\n        width: 100%;\n        bottom: 0;\n        left: 0;\n        padding: 15px 30px;\n        font-size: 13px;\n        font-weight: 300;\n        color: $color-text-primary-invert;\n    }\n    .social-icons a {\n        font-size: 12px;\n        margin-right: 10px;\n        position: relative;\n    }\n    &.nav {\n        width: 95%;\n    }\n}\n\n.nav.child_menu {\n    display: block;\n}\n\n@media screen and (max-width: 767px) {\n    .row-offcanvas {\n        position: relative;\n        -webkit-transition: all 0.25s ease-out;\n        -moz-transition: all 0.25s ease-out;\n        transition: all 0.25s ease-out;\n    }\n    .row-offcanvas-right .sidebar-offcanvas {\n        right: -41.6%;\n    }\n    .row-offcanvas-left .sidebar-offcanvas {\n        left: -41.6%;\n    }\n    .row-offcanvas-right.active {\n        right: 41.6%;\n    }\n    .row-offcanvas-left.active {\n        left: 41.6%;\n    }\n    .sidebar-offcanvas {\n        position: absolute;\n        top: 0;\n        width: 41.6%;\n    }\n    #sidebar {\n        padding-top: 0;\n    }\n}\n\n@media (max-width: 991px) {\n    .sidebar {\n        position: relative;\n        z-index: 99999;\n        height: auto;\n        width: 100%;\n        padding: 0;\n        &:after {\n            content: normal;\n        }\n        .copyright {\n            display: none;\n        }\n    }\n    .wrapper {\n        margin-left: 0;\n    }\n}","@import url(\"https://fonts.googleapis.com/css?family=Racing+Sans+One\");\n@import url(\"https://fonts.googleapis.com/css?family=Roboto:300,400,600,700\");\n// @import url('https://fonts.googleapis.com/css?family=Alegreya+Sans:400,500,700');\n@import url(\"https://fonts.googleapis.com/icon?family=Material+Icons\");\n@import \"./variables.scss\";\n@import \"./layout.scss\";\n@import \"./sidebar.scss\";\n\n/*! -----------------------------------------------------------------\n[Master Stylesheet]\n\nProject:     Ajaxtown\nVersion:     1.0\nLast change: 21/03/2017\nPrimary use: Blog\n\n------------------------------------------------------------------\n[General Styles]\n*/\n\nhtml {\n    height: 100%;\n    font-family: \"Roboto\", sans-serif;\n}\n\nbody {\n    background: $color-bg-primary;\n    line-height: 1.8;\n    font-size: 14px;\n    font-weight: 400;\n    color: $color-text-primary;\n    -ms-overflow-style: scrollbar;\n    overflow-x: hidden;\n    height: 100%;\n}\n\nimg {\n    max-width: 100%;\n    height: auto;\n}\n\niframe {\n    border: 0;\n}\n\n[contenteditable=\"true\"]:empty:before {\n    content: attr(placeholder);\n    display: block;\n    color: #eee;\n    /* For Firefox */\n}\n\ninput,\ntextarea,\n[contenteditable=\"true\"]:focus {\n    outline: none;\n}\n\n::-webkit-input-placeholder {\n    font-size: 12px !important;\n}\n\n.top-head {\n    display: none;\n}\n\n.home .top-head {\n    display: block;\n}\n\n.container-fluid {\n    padding: 0 24px;\n}\n\n.pointer {\n    cursor: pointer;\n}\n\n.fa {\n    position: relative;\n}\n\n\n/*!------------------------------------------------------------------\n[Typography]\n*/\n\na {\n    color: $link_active;\n    -webkit-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    -moz-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    -o-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n}\n\na:hover,\na:focus {\n    text-decoration: none;\n    outline: none;\n    color: $link_hover;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    font-family: \"Roboto Condensed\", sans-serif;\n    text-transform: uppercase;\n    letter-spacing: 2px;\n    font-weight: 400;\n    color: #111;\n    margin: 0 0 20px;\n}\n\nh1 {\n    font-size: 20px;\n}\n\nh2 {\n    font-size: 18px;\n}\n\nh4 {\n    font-size: 16px;\n}\n\nh5 {\n    font-size: 14px;\n}\n\nh6 {\n    font-size: 12px;\n}\n\np,\nul,\nol {\n    margin: 0 0 30px;\n}\n\npre {\n    border: 0;\n    line-height: 1.9;\n    font-size: 12px;\n    padding: 35px;\n    -webkit-border-radius: 2px;\n    -moz-border-radius: 2px;\n    -o-border-radius: 2px;\n    border-radius: 2px;\n}\n\nblockquote {\n    border: 0;\n    font-family: Georgia, sans-serif;\n    text-align: center;\n    font-style: italic;\n    font-size: 28px;\n    color: #111;\n    padding: 0;\n    margin: 0;\n}\n\nblockquote:before {\n    display: block;\n    content: \"\\f10d\";\n    margin: 0 0 30px;\n    font: normal normal normal 14px/1 FontAwesome;\n    font-size: 24px;\n    color: #bbb;\n    -webkit-font-smoothing: antialiased;\n    text-rendering: auto;\n    opacity: 0.3;\n}\n\nblockquote cite {\n    display: block;\n    font-size: 17px;\n    color: #bbb;\n    margin: 30px 0;\n}\n\n.lead {\n    line-height: 1.8;\n    font-size: 18px;\n}\n\n.font-alt {\n    text-transform: uppercase;\n    letter-spacing: 2px;\n}\n\n.font-serif {\n    font-family: Georgia, sans-serif;\n    font-style: italic;\n}\n\n.text-light {\n    color: #fff !important;\n}\n\n.text-light h1,\n.text-light h2,\n.text-light h3,\n.text-light h4,\n.text-light h5,\n.text-light h6 {\n    color: #fff !important;\n}\n\n.text-light a {\n    color: rgba(255, 255, 255, 0.7);\n}\n\n.text-light a:hover {\n    color: #fff;\n}\n\n.brand {\n    font-family: $racing_font;\n}\n\n\n/*!------------------------------------------------------------------\n[Preloader]\n*/\n\n.page-loader {\n    background: #fff;\n    position: fixed;\n    top: 0;\n    bottom: 0;\n    right: 0;\n    left: 0;\n    z-index: 9998;\n}\n\n.loader {\n    position: absolute;\n    width: 50px;\n    height: 50px;\n    top: 50%;\n    left: 50%;\n    margin: -25px 0 0 -25px;\n    text-indent: -9999em;\n    background-color: transparent;\n    border: 5px solid #eceff1;\n    -webkit-border-radius: 50%;\n    -moz-border-radius: 50%;\n    -o-border-radius: 50%;\n    border-radius: 50%;\n}\n\n.loader:after {\n    position: absolute;\n    content: \"\";\n    top: 0px;\n    left: 0px;\n    width: 40px;\n    height: 40px;\n    padding: 2px;\n    border-width: 2px;\n    border-style: solid;\n    border-color: #111 transparent transparent transparent;\n    -webkit-border-radius: 50%;\n    -moz-border-radius: 50%;\n    -o-border-radius: 50%;\n    border-radius: 50%;\n    box-sizing: border-box;\n    -webkit-box-sizing: border-box;\n    animation: ringrotate 0.8s infinite cubic-bezier(0.5, 0.49, 0.49, 0.49);\n    -webkit-animation: ringrotate 0.8s infinite cubic-bezier(0.5, 0.49, 0.49, 0.49);\n}\n\n@keyframes ringrotate {\n    0% {\n        -webkit-transform: rotateZ(0deg);\n        -moz-transform: rotateZ(0deg);\n        -o-transform: rotateZ(0deg);\n        transform: rotateZ(0deg);\n    }\n    100% {\n        -webkit-transform: rotateZ(360deg);\n        -moz-transform: rotateZ(360deg);\n        -o-transform: rotateZ(360deg);\n        transform: rotateZ(360deg);\n    }\n}\n\n@-webkit-keyframes ringrotate {\n    0% {\n        -webkit-transform: rotateZ(0deg);\n        -moz-transform: rotateZ(0deg);\n        -o-transform: rotateZ(0deg);\n        transform: rotateZ(0deg);\n    }\n    100% {\n        -webkit-transform: rotateZ(360deg);\n        -moz-transform: rotateZ(360deg);\n        -o-transform: rotateZ(360deg);\n        transform: rotateZ(360deg);\n    }\n}\n\n\n/*!------------------------------------------------------------------\n[Forms]\n*/\n\n.form-control {\n    border: 0;\n    height: 36px;\n    padding: 13px 0px;\n    font-size: 13px;\n    border-bottom: 1px solid $color-border;\n    color: $color-text-secondary-light;\n    border-radius: 0px;\n    -webkit-box-shadow: none;\n    -moz-box-shadow: none;\n    -o-box-shadow: none;\n    box-shadow: none;\n    transition: 0.3s border-color linear;\n    text-transform: none;\n    background: transparent;\n}\n\n.form-control[disabled] {\n    background: transparent;\n    color: $color-text-primary-light;\n}\n\n.form-control:focus {\n    box-shadow: 0px 1px 0px 0px #66afe9;\n}\n\n.comments-form .control-label {\n    font-weight: 400;\n    font-size: 12px;\n    color: #bbb;\n}\n\n@media (min-width: 768px) {\n    .comments-form .control-label {\n        padding-top: 12px;\n    }\n}\n\n\n/*!------------------------------------------------------------------\n[Buttons]\n*/\n\n.btn {\n    font-family: \"Roboto Condensed\", sans-serif;\n    text-transform: uppercase;\n    letter-spacing: 2px;\n    font-weight: 400;\n    font-size: 11px;\n    padding: 9px 28px;\n    -webkit-border-radius: 0;\n    -moz-border-radius: 0;\n    -o-border-radius: 0;\n    border-radius: 0;\n}\n\n.btn:focus,\n.btn:active:focus,\n.btn.active:focus,\n.btn.focus,\n.btn:active.focus,\n.btn.active.focus {\n    outline: 0;\n}\n\n.btn-dark,\n.btn-dark:focus {\n    background: rgba(17, 17, 17, 1);\n    color: #fff;\n}\n\n.btn-dark:hover {\n    background: rgba(17, 17, 17, 0.8);\n    color: #fff;\n}\n\n.btn-blue,\n.btn-blue:focus {\n    background: $link_active;\n    color: #fff;\n}\n\n.btn-blue:hover {\n    background: $link_hover;\n    color: #fff;\n}\n\n\n/*! Button types */\n\n.btn.btn-round {\n    -webkit-border-radius: 2px;\n    -moz-border-radius: 2px;\n    -o-border-radius: 2px;\n    border-radius: 2px;\n}\n\n.btn.btn-circle {\n    -webkit-border-radius: 30px;\n    -moz-border-radius: 30px;\n    -o-border-radius: 30px;\n    border-radius: 30px;\n}\n\n\n/*! Button sizes */\n\n.btn.btn-lg {\n    letter-spacing: 3px;\n    font-size: 12px;\n    padding: 12px 45px;\n}\n\n.btn.btn-sm {\n    letter-spacing: 1px;\n    font-size: 10px;\n    padding: 6px 25px;\n}\n\n.btn.btn-xs {\n    letter-spacing: 1px;\n    font-size: 9px;\n    padding: 4px 19px;\n}\n\n\n/*!------------------------------------------------------------------\n[Bars]\n*/\n\n.progress {\n    overflow: visible;\n    height: 4px;\n}\n\n.progress-bar {\n    position: relative;\n    background: #111;\n    -webkit-box-shadow: none;\n    -moz-box-shadow: none;\n    -o-box-shadow: none;\n    box-shadow: none;\n    -webkit-border-radius: 2px;\n    -moz-border-radius: 2px;\n    -o-border-radius: 2px;\n    border-radius: 2px;\n}\n\n.progress-title {\n    font-size: 12px;\n    margin: 0 0 12px;\n}\n\n.progress-bar>span {\n    position: absolute;\n    background: #111;\n    padding: 2px 10px;\n    top: -12px;\n    right: 0;\n    font-size: 11px;\n    -webkit-border-radius: 2px;\n    -moz-border-radius: 2px;\n    -o-border-radius: 2px;\n    border-radius: 2px;\n}\n\n.progress-bar.progress-bar-success {\n    background-color: #5cb85c;\n}\n\n.progress-bar.progress-bar-success>span {\n    background-color: #5cb85c;\n}\n\n.progress-bar.progress-bar-info {\n    background-color: #5bc0de;\n}\n\n.progress-bar.progress-bar-info>span {\n    background-color: #5bc0de;\n}\n\n.progress-bar.progress-bar-warning {\n    background-color: #f0ad4e;\n}\n\n.progress-bar.progress-bar-warning>span {\n    background-color: #f0ad4e;\n}\n\n.progress-bar.progress-bar-danger {\n    background-color: #d9534f;\n}\n\n.progress-bar.progress-bar-danger>span {\n    background-color: #d9534f;\n}\n\n\n/*!------------------------------------------------------------------\n[Pagination]\n*/\n\n.pagination>li>a,\n.pagination>li>span {\n    background: #f5f5f5;\n    border: 0;\n    color: #111;\n    padding: 9px 14px;\n    margin: 0 5px 0 0;\n}\n\n.pagination>li>a,\n.pagination>li>span,\n.pagination>li:first-child>a,\n.pagination>li:first-child>span,\n.pagination>li:last-child>a,\n.pagination>li:last-child>span {\n    -webkit-border-radius: 2px;\n    -moz-border-radius: 2px;\n    -o-border-radius: 2px;\n    border-radius: 2px;\n    -webkit-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    -moz-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    -o-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n}\n\n.pagination>li>a:hover,\n.pagination>li>a:focus,\n.pagination>li>span:hover,\n.pagination>li>span:focus,\n.pagination>.active>a,\n.pagination>.active>span,\n.pagination>.active>a:hover,\n.pagination>.active>span:hover,\n.pagination>.active>a:focus,\n.pagination>.active>span:focus {\n    background-color: #111;\n    color: #fff;\n}\n\n\n/*!------------------------------------------------------------------\n[Tags]\n*/\n\n.tags a {\n    position: relative;\n    background: #f5f5f5;\n    display: inline-block;\n    padding: 6px 20px 5px 10px;\n    margin: 0px 15px 10px 0;\n    font-size: 11px;\n    -webkit-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    -moz-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    -o-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    -webkit-border-radius: 2px 0 0 2px;\n    -moz-border-radius: 2px 0 0 2px;\n    -o-border-radius: 2px 0 0 2px;\n    border-radius: 2px 0 0 2px;\n}\n\n.tags a:before {\n    border-top: 13px solid rgba(0, 0, 0, 0);\n    border-left: 9px solid #f5f5f5;\n    border-bottom: 13px solid rgba(0, 0, 0, 0);\n    position: absolute;\n    content: \"\";\n    right: -9px;\n    top: 2px;\n    height: 0;\n    width: 0;\n    -webkit-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    -moz-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    -o-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n}\n\n.tags a:after {\n    background: #fff;\n    position: absolute;\n    display: block;\n    content: \"\";\n    right: 4px;\n    top: 12px;\n    height: 4px;\n    width: 4px;\n    -webkit-border-radius: 50%;\n    -moz-border-radius: 50%;\n    -o-border-radius: 50%;\n    border-radius: 50%;\n}\n\n.tags a:hover {\n    background: #eee;\n}\n\n.tags a:hover:before {\n    border-left-color: #eee;\n}\n\n\n/*!------------------------------------------------------------------\n[Tabs]\n*/\n\n.nav-tabs {\n    border-color: #eee;\n}\n\n.nav-tabs>li>a {\n    font-weight: 400;\n    font-size: 13px;\n    color: #111;\n    -webkit-border-radius: 2px 2px 0 0;\n    -moz-border-radius: 2px 2px 0 0;\n    -o-border-radius: 2px 2px 0 0;\n    border-radius: 2px 2px 0 0;\n}\n\n.nav-tabs>li.active>a,\n.nav-tabs>li.active>a:hover,\n.nav-tabs>li.active>a:focus {\n    border-color: #eee;\n    border-bottom: 1px solid transparent;\n    color: #111;\n}\n\n.tab-content {\n    padding: 15px 0;\n}\n\n\n/*!------------------------------------------------------------------\n[Accordions]\n*/\n\n.panel-title,\n.panel-title a {\n    font-weight: 400;\n    font-size: 13px;\n    color: #111;\n}\n\n.panel-group {\n    border: 1px solid #eee;\n    border-bottom: 0;\n    -webkit-border-radius: 2px;\n    -moz-border-radius: 2px;\n    -o-border-radius: 2px;\n    border-radius: 2px;\n}\n\n.panel-group .panel {\n    border-radius: 0;\n}\n\n.panel {\n    border: 0;\n    border-bottom: 1px solid #eee;\n}\n\n.panel-default>.panel-heading+.panel-collapse>.panel-body {\n    border-color: #eee;\n}\n\n.panel-heading {\n    padding: 15px 20px;\n}\n\n.panel-default>.panel-heading {\n    background: transparent;\n    border-color: #eee;\n}\n\n.panel-heading a {\n    position: relative;\n    display: block;\n}\n\n.panel-heading a:after {\n    position: absolute;\n    content: \"\\f068\";\n    top: 50%;\n    right: 0px;\n    margin-top: -5px;\n    font-family: \"FontAwesome\";\n    font-size: 11px;\n    line-height: 1;\n}\n\n.panel-heading a.collapsed:after {\n    content: \"\\f067\";\n}\n\n.panel-title {\n    font-size: 12px;\n}\n\n.panel-group .panel+.panel {\n    border-top: 0;\n    margin-top: 0;\n}\n\n.panel-arrow .panel-heading a:after {\n    content: \"\\f106\";\n}\n\n.panel-arrow .panel-heading a.collapsed:after {\n    content: \"\\f107\";\n}\n\n\n/*!------------------------------------------------------------------\n[Switch]\n*/\n\n.switch-block {\n    display: flex;\n    align-items: center;\n    .switch {\n        margin: 0 20px;\n    }\n    .switch-label {\n        text-transform: uppercase;\n        letter-spacing: 1px;\n        font-size: 12px;\n        font-weight: 500;\n        color: $color-text-primary-light;\n        transition: 0.2s color linear;\n    }\n    &.on .switch-on-text {\n        color: $color-text-secondary;\n    }\n    &.off .switch-off-text {\n        color: $color-text-secondary;\n    }\n}\n\n\n/*!------------------------------------------------------------------\n[Button Together]\n*/\n\n.btn-together {\n    display: flex;\n    .btn-item:not(:first-child) {\n        margin-left: 4px;\n    }\n}\n\n\n/*!------------------------------------------------------------------\n[Nice Checkboxes and Radio Buttons]\n*/\n\n.control {\n    display: block;\n    position: relative;\n    padding-left: 30px;\n    margin-bottom: 16px;\n    cursor: pointer;\n    font-size: 18px;\n    input {\n        position: absolute;\n        z-index: -1;\n        opacity: 0;\n    }\n}\n\n.control__indicator {\n    position: absolute;\n    top: 2px;\n    left: 0;\n    height: 20px;\n    width: 20px;\n    background: #e6e6e6;\n}\n\n.control--radio .control__indicator {\n    border-radius: 50%;\n}\n\n.control {\n    &:hover input~.control__indicator {\n        background: #ccc;\n    }\n    input {\n        &:focus~.control__indicator {\n            background: #ccc;\n        }\n        &:checked~.control__indicator {\n            background: #111111;\n        }\n    }\n    &:hover input:not([disabled]):checked~.control__indicator {\n        background: #111111;\n    }\n    input {\n        &:checked:focus~.control__indicator {\n            background: #111111;\n        }\n        &:disabled~.control__indicator {\n            background: #e6e6e6;\n            opacity: 0.6;\n            pointer-events: none;\n        }\n    }\n}\n\n.control__indicator:after {\n    content: \"\";\n    position: absolute;\n    display: none;\n}\n\n.control input:checked~.control__indicator:after {\n    display: block;\n}\n\n.control--checkbox {\n    .control__indicator:after {\n        left: 8px;\n        top: 4px;\n        width: 3px;\n        height: 8px;\n        border: solid #fff;\n        border-width: 0 2px 2px 0;\n        transform: rotate(45deg);\n    }\n    input:disabled~.control__indicator:after {\n        border-color: #7b7b7b;\n    }\n}\n\n.control--radio {\n    .control__indicator:after {\n        left: 7px;\n        top: 7px;\n        height: 6px;\n        width: 6px;\n        border-radius: 50%;\n        background: #fff;\n    }\n    input:disabled~.control__indicator:after {\n        background: #7b7b7b;\n    }\n}\n\n\n/*!------------------------------------------------------------------\n[Sliders]\n*/\n\n.switch {\n    position: relative;\n    display: inline-block;\n    width: 46px;\n    height: 18px;\n    input {\n        display: none;\n    }\n}\n\n.slider {\n    position: absolute;\n    cursor: pointer;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    background-color: #ccc;\n    -webkit-transition: 0.4s;\n    transition: 0.4s;\n    &:before {\n        position: absolute;\n        content: \"\";\n        height: 26px;\n        width: 26px;\n        left: -4px;\n        bottom: -3px;\n        background-color: white;\n        -webkit-transition: 0.4s;\n        transition: 0.4s;\n        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.28);\n    }\n}\n\ninput {\n    &:checked+.slider {\n        background-color: $color-accent;\n    }\n    &:focus+.slider {\n        box-shadow: 0 0 1px $color-accent;\n    }\n    &:checked+.slider:before {\n        -webkit-transform: translateX(26px);\n        -ms-transform: translateX(26px);\n        transform: translateX(26px);\n    }\n}\n\n.slider.round {\n    border-radius: 34px;\n    &:before {\n        border-radius: 50%;\n    }\n}\n\n@media (max-width: 991px) {\n    .slides-container .hero-caption {\n        padding-right: 0;\n    }\n}\n\n\n/*!------------------------------------------------------------------\n[Navbar]\n*/\n\n.navbar-custom {\n    background: transparent;\n    border: 0;\n    padding: 0;\n    margin: 0;\n    .navbar-brand {\n        display: table-cell;\n        vertical-align: middle;\n        float: none;\n        height: 70px;\n        padding-top: 0;\n        padding-bottom: 0;\n        font-weight: 400;\n        font-size: 23px;\n        color: $color-text-primary-invert;\n        width: 100vw;\n        text-shadow: 1px 1px 0 #000;\n        small {\n            display: block;\n            font-size: 12px;\n            font-style: italic;\n            font-family: \"Source Sans Pro\", sans-serif;\n        }\n    }\n    li>a,\n    .dropdown-menu li>a {\n        color: $color-menu-link;\n    }\n    .nav {\n        >li>a {\n            &:hover,\n            &:focus {\n                background: transparent;\n                color: #fff;\n            }\n        }\n        .open>a {\n            background: transparent;\n            &:hover,\n            &:focus {\n                background: transparent;\n                color: #fff;\n            }\n        }\n    }\n    .dropdown-menu {\n        >li>a {\n            &:hover,\n            &:focus {\n                background: transparent;\n                color: $color-accent;\n            }\n        }\n        background: rgba(26,\n        26,\n        26,\n        0.9);\n        border: 0;\n        padding: 5px 0;\n        -webkit-box-shadow: none;\n        -moz-box-shadow: none;\n        -o-box-shadow: none;\n        box-shadow: none;\n        -webkit-border-radius: 0;\n        -moz-border-radius: 0;\n        -o-border-radius: 0;\n        border-radius: 0;\n    }\n    .navbar-toggle {\n        .icon-bar {\n            background: #fff;\n        }\n        padding: 0;\n        margin: 27px 15px 0 0;\n    }\n    .navbar-nav {\n        margin-top: 0;\n        margin-bottom: 0;\n        padding: 0px 0 0px 20px;\n    }\n    .navbar-collapse {\n        -webkit-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n        -moz-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n        -o-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n        box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n    }\n    .navbar-nav {\n        i {\n            width: 12px;\n        }\n        .menu-icon {\n            margin-left: -20px;\n            margin-top: 6px;\n            font-size: 12px;\n            color: $color-accent;\n        }\n        >li.item a {\n            margin-left: 10px;\n        }\n        ul {\n            list-style: none;\n        }\n        .parent {\n            padding-bottom: 4px;\n        }\n        li ul li {\n            //padding: 6px 0px;\n        }\n    }\n}\n\n@media (min-width: 992px) {\n    .navbar-custom {\n        .navbar-header {\n            float: none;\n            margin: 0 0 60px;\n            padding: 20px 20px;\n        }\n        .navbar-nav {\n            float: none;\n            li {\n                float: none;\n            }\n        }\n        li>a,\n        .dropdown-menu li>a {\n            padding-top: 12px;\n            padding-bottom: 12px;\n        }\n        li.dropdown>a:after {\n            position: absolute;\n            display: block;\n            right: 35px;\n            top: 50%;\n            margin-top: -6px;\n            font: normal normal normal 14px/1 FontAwesome;\n            font-size: 10px;\n            content: \"\\f105\";\n            text-rendering: auto;\n            -webkit-font-smoothing: antialiased;\n            -moz-osx-font-smoothing: grayscale;\n        }\n        .dropdown-menu {\n            top: -5px;\n            left: 100%;\n            min-width: 200px;\n        }\n        .navbar-collapse {\n            padding-left: 0;\n            padding-right: 0;\n            box-shadow: none;\n        }\n    }\n}\n\n@media (max-width: 991px) {\n    .navbar-custom li>a {\n        -webkit-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n        -moz-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n        -o-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n        box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n    }\n    .navbar-custom .navbar-nav .open .dropdown-menu>li>a,\n    .navbar-custom .navbar-nav .open .dropdown-menu .dropdown-header {\n        padding: 10px 15px 10px 25px;\n    }\n    .navbar-nav>li>.dropdown-menu {\n        padding-top: 0;\n        padding-bottom: 0;\n    }\n}\n\n\n/*!------------------------------------------------------------------\n[Module Hero]\n*/\n\n.module-hero {\n    position: relative;\n    overflow: hidden;\n    background-attachment: fixed;\n    background-repeat: no-repeat;\n    background-position: center center;\n    -webkit-background-size: cover;\n    -moz-background-size: cover;\n    -o-background-size: cover;\n    background-size: cover;\n}\n\n.hero-caption {\n    position: relative;\n    display: table;\n    height: 100%;\n    width: 80%;\n    text-align: center;\n    margin: 0 auto;\n    z-index: 2;\n}\n\n.hero-text {\n    position: relative;\n    display: table-cell;\n    vertical-align: middle;\n    height: 100%;\n}\n\n.hero-subtitle {\n    font-size: 16px;\n    margin: 0 0 40px;\n}\n\n.module-hero h1,\n.module-hero h2,\n.module-hero h3,\n.module-hero h4,\n.module-hero h5,\n.module-hero h6 {\n    line-height: 1.4;\n    margin: 0 0 20px;\n}\n\n.module-hero h1 {\n    letter-spacing: 20px;\n    font-weight: 300;\n    font-size: 54px;\n}\n\n.module-hero h2 {\n    letter-spacing: 16px;\n    font-weight: 300;\n    font-size: 48px;\n}\n\n.module-hero h3 {\n    letter-spacing: 12px;\n    font-size: 32px;\n}\n\n.module-hero h4 {\n    letter-spacing: 10px;\n    font-size: 18px;\n}\n\n.module-hero h5 {\n    letter-spacing: 8px;\n    font-size: 14px;\n}\n\n.module-hero h6 {\n    letter-spacing: 6px;\n    font-size: 12px;\n}\n\n@media (max-width: 767px) {\n    .module-hero h1 {\n        font-size: 38px;\n    }\n    .module-hero h2 {\n        font-size: 30px;\n    }\n    .module-hero h3 {\n        letter-spacing: 12px;\n        font-size: 22px;\n    }\n}\n\n@media (max-width: 667px) {\n    .module-hero h1 {\n        letter-spacing: 10px;\n        font-size: 30px;\n    }\n    .module-hero h2 {\n        letter-spacing: 8px;\n        font-size: 22px;\n    }\n    .module-hero h3 {\n        letter-spacing: 6px;\n        font-size: 18px;\n    }\n    .module-hero h4 {\n        letter-spacing: 4px;\n        font-size: 16px;\n    }\n    .module-hero h5 {\n        letter-spacing: 2px;\n        font-size: 14px;\n    }\n    .module-hero h6 {\n        letter-spacing: 2px;\n        font-size: 12px;\n    }\n}\n\n\n/*!------------------------------------------------------------------\n[Card]\n*/\n\n.card {\n    background: #fff;\n    padding: 20px;\n    margin-bottom: 20px;\n}\n\n\n/*!------------------------------------------------------------------\n[Modules]\n*/\n\n.module,\n.module-sm,\n.module-xs {\n    background-attachment: fixed;\n    padding: 140px 0;\n    position: relative;\n    background-repeat: no-repeat;\n    background-position: center center;\n    -webkit-background-size: cover;\n    -moz-background-size: cover;\n    -o-background-size: cover;\n    background-size: cover;\n}\n\n.module-sm {\n    padding: 70px 0;\n}\n\n.module-xs {\n    padding: 35px 0;\n    margin: 0 30px;\n}\n\n.module-title {\n    line-height: 1.4;\n    font-weight: 400;\n    font-size: 17px;\n    color: $color-text-secondary;\n}\n\n.module-subtitle {\n    font-family: \"Roboto\", sans-serif;\n    line-height: 1.8;\n    font-weight: 300;\n    font-size: 13px;\n    color: $color-text-primary-light;\n}\n\n.module-title,\n.module-subtitle {\n    margin: 0 0 30px;\n}\n\n.module-title+.module-subtitle {\n    margin-top: -25px;\n}\n\n\n/*!------------------------------------------------------------------\n[Overlays]\n*/\n\n.bg-gray {\n    background-color: #f5f5f5;\n}\n\n.bg-film:before,\n.bg-dark-30:before,\n.bg-dark-60:before,\n.bg-dark-90:before {\n    position: absolute;\n    height: 100%;\n    width: 100%;\n    z-index: 0;\n}\n\n.bg-dark-30:before {\n    background: rgba(34, 34, 34, 0.3);\n    content: \"\";\n    left: 0;\n    top: 0;\n}\n\n.bg-dark-60:before {\n    background: rgba(34, 34, 34, 0.6);\n    content: \"\";\n    left: 0;\n    top: 0;\n}\n\n.bg-dark-90:before {\n    background: rgba(34, 34, 34, 0.9);\n    content: \"\";\n    left: 0;\n    top: 0;\n}\n\n.bg-film:before {\n    //background-image:url(../images/dot.gif);\n    opacity: 0.15;\n    content: \"\";\n    left: 0;\n    top: 0;\n}\n\n.bg-film,\n.bg-dark-30,\n.bg-dark-60,\n.bg-dark-90 {\n    color: #fff;\n}\n\n.bg-film h1,\n.bg-film h2,\n.bg-film h3,\n.bg-film h4,\n.bg-film h5,\n.bg-film h6,\n.bg-film .module-subtitle,\n.bg-dark-30 h1,\n.bg-dark-30 h2,\n.bg-dark-30 h3,\n.bg-dark-30 h4,\n.bg-dark-30 h5,\n.bg-dark-30 h6,\n.bg-dark-30 .module-subtitle,\n.bg-dark-60 h1,\n.bg-dark-60 h2,\n.bg-dark-60 h3,\n.bg-dark-60 h4,\n.bg-dark-60 h5,\n.bg-dark-60 h6,\n.bg-dark-60 .module-subtitle,\n.bg-dark-90 h1,\n.bg-dark-90 h2,\n.bg-dark-90 h3,\n.bg-dark-90 h4,\n.bg-dark-90 h5,\n.bg-dark-90 h6,\n.bg-dark-90 .module-subtitle {\n    color: #fff;\n}\n\n.divider {\n    border-color: #f5f5f5;\n    margin: 0;\n}\n\n\n/*!------------------------------------------------------------------\n[Post]\n*/\n\n.post {\n    margin-bottom: 60px;\n}\n\n.post-header {\n    margin: 40px 0 30px;\n}\n\n.post-content {\n    padding-bottom: 20px;\n    padding: 0px;\n}\n\n.post-title {\n    font-weight: 700;\n    font-size: 16px;\n    color: #111;\n    min-height: 20px;\n    margin: 0 0 10px;\n    a {\n        font-weight: 700;\n        font-size: 16px;\n        color: #111;\n        margin: 0 0 10px;\n    }\n}\n\n.post {\n    h1:first-child,\n    h2:first-child,\n    h3:first-child,\n    h4:first-child,\n    h5:first-child,\n    h6:first-child {\n        margin-top: 0px;\n    }\n    h1,\n    h2,\n    h3,\n    h4,\n    h5,\n    h6 {\n        margin-top: 40px;\n    }\n    p>img {\n        width: 100%;\n        max-width: none;\n    }\n}\n\n.post-thumbnail img {\n    width: 100%;\n    max-width: none;\n}\n\n.post {\n    .slider {\n        img {\n            margin-left: 0;\n            margin-right: 0;\n        }\n        &.owl-carousel {\n            width: 120%;\n            margin-left: -10%;\n            margin-right: -10%;\n        }\n    }\n    .fluid-width-video-wrapper {\n        width: 120%;\n        margin-left: -10%;\n        margin-right: -10%;\n    }\n    .gallery {\n        margin-left: -10%;\n        margin-right: -10%;\n    }\n    blockquote {\n        margin: 60px -10%;\n    }\n}\n\n@media (max-width: 991px) {\n    .post p>img,\n    .post-thumbnail img {\n        max-width: 100%;\n        margin-left: 0;\n        margin-right: 0;\n    }\n    .post {\n        .slider.owl-carousel,\n        .fluid-width-video-wrapper {\n            max-width: 100%;\n            margin-left: 0;\n            margin-right: 0;\n        }\n        pre,\n        .gallery,\n        blockquote {\n            margin-left: 0;\n            margin-right: 0;\n        }\n    }\n}\n\n\n/*!------------------------------------------------------------------\n[Post Navigation]\n*/\n\n.post-navigation {\n    display: block;\n    background-color: rgba(34, 34, 34, 0.5);\n    text-align: center;\n    padding: 140px 20px;\n    -webkit-transition: all 0.3s;\n    -moz-transition: all 0.3s;\n    -o-transition: all 0.3s;\n    transition: all 0.3s;\n}\n\n.post-navigation:hover {\n    background-color: rgba(34, 34, 34, 0.7);\n}\n\n.post-navigation {\n    color: #fff;\n}\n\n.post-navigation h1,\n.post-navigation h2,\n.post-navigation h3,\n.post-navigation h4,\n.post-navigation h5,\n.post-navigation h6 {\n    color: #fff !important;\n}\n\n.post-nav-title {\n    letter-spacing: 6px;\n    font-size: 32px;\n    margin-bottom: 0;\n}\n\n.post-nav-subtitle {\n    font-size: 13px;\n}\n\n\n/*!------------------------------------------------------------------\n[Comments and Comment form]\n*/\n\n.comment-avatar {\n    float: left;\n    max-width: 50px;\n}\n\n.comment-avatar img {\n    -webkit-border-radius: 50%;\n    -moz-border-radius: 50%;\n    -o-border-radius: 50%;\n    border-radius: 50%;\n}\n\n.comment-content {\n    margin: 0 0 40px 70px;\n    padding: 0 0 25px;\n}\n\n.comment-author {\n    font-size: 11px;\n    margin: 0 0 20px;\n}\n\n.comment-author a {\n    color: #111;\n}\n\n.comment-meta {\n    font-size: 10px;\n}\n\n.post-author {\n    margin: 80px 0 0;\n}\n\n.comment-title,\n.comment-form-title,\n.post-author-title {\n    font-size: 15px;\n    margin: 0 0 20px;\n}\n\n.comments+.comment-form {\n    margin: 70px 0 0;\n}\n\n@media (min-width: 768px) {\n    .comment .comment {\n        margin-left: 70px;\n    }\n}\n\n\n/*!------------------------------------------------------------------\n[Footer]\n*/\n\n.footer {\n    background-color: #fff;\n    padding: 20px 0;\n    position: relative;\n    background-repeat: no-repeat;\n    background-position: center center;\n    -webkit-background-size: cover;\n    -moz-background-size: cover;\n    -o-background-size: cover;\n    background-size: cover;\n    color: #bbb;\n}\n\n.contact-info {\n    text-align: center;\n    list-style: none;\n    padding: 0;\n    margin: 0 0 40px;\n}\n\n.contact-info li {\n    display: inline-block;\n    margin: 5px 15px;\n}\n\n.to-top-link {\n    position: absolute;\n    bottom: 0;\n    left: 50%;\n    width: 40px;\n    height: 40px;\n    background: #f5f5f5;\n    text-align: center;\n    line-height: 40px;\n    margin-left: -20px;\n}\n\n.to-top-link:hover {\n    background: #111;\n    color: #fff;\n}\n\n.loader {\n    margin: 0 0 2em;\n    height: 100px;\n    width: 20%;\n    text-align: center;\n    padding: 1em;\n    margin: 0 auto 1em;\n    display: inline-block;\n    vertical-align: top;\n}\n\n\n/*----------------------------------\n[Material Design]\n*/\n\n.list-item {\n    font-size: 14px !important;\n    >div>div {\n        padding-top: 12px !important;\n        padding-bottom: 12px !important;\n    }\n    .material-icons {\n        font-size: 18px !important;\n    }\n}\n\n.featured-image {\n    label {\n        position: absolute !important;\n        margin-top: -53px !important;\n        width: initial !important;\n    }\n}\n\ninput,\ntextarea,\n[role=\"menuitem\"] {\n    font-size: 14px !important;\n}\n\nul {\n    margin: 0px;\n    list-style-type: none;\n    padding: 0px;\n}\n\n.md-btn {\n    button {\n        span {\n            font-weight: 400 !important;\n        }\n    }\n}\n\n.md-btn-xs {\n    min-width: 66px !important;\n    height: 25px !important;\n    line-height: 25px !important;\n    button {\n        height: 25px !important;\n        line-height: 25px !important;\n        div>div {\n            height: 25px !important;\n            line-height: 25px !important;\n        }\n        span {\n            font-size: 12px !important;\n        }\n    }\n    i {\n        font-size: 14px !important;\n    }\n}\n\n.inline-edit {\n    height: 24px;\n    width: 100%;\n    display: block;\n    &[contentEditable=\"true\"] {\n        border-bottom: 1px solid #66afe9;\n    }\n}","/*! -----------------------------------------------------------------\n[Master Stylesheet for Client]\n\nProject:     Ajaxtown\nVersion:     1.0\nLast change: 21/03/2017\nPrimary use: Blog\n\n------------------------------------------------------------------\n[Sidebar Styles]\n*/\n@import url(\"https://fonts.googleapis.com/css?family=Racing+Sans+One\");\n@import url(\"https://fonts.googleapis.com/css?family=Roboto:300,400,600,700\");\n@import url(\"https://fonts.googleapis.com/icon?family=Material+Icons\");\n:root {\n  --color-text-primary: #333333;\n  --color-text-primary-invert: #b7b7b7;\n  --color-text-primary-light: #616161;\n  --color-text-muted: #b9b9b9;\n  --color-text-secondary: #000000;\n  --color-text-secondary-light: #4c4c4c;\n  --color-accent: #2196f3;\n  --color-bg-primary: #f1f1f1;\n  --color-bg-secondary: #1f1e1e;\n  --color-border: #e5e5e5;\n  --link-hover: #1a82d6;\n  --color-menu-link: var(--color-text-primary-invert); }\n\n/*!------------------------------------------------------------------\n[Body Color]\n*/\n/*!------------------------------------------------------------------\n[Headlines]\n*/\n/*!------------------------------------------------------------------\n[Help text]\n*/\n/*!------------------------------------------------------------------\n[Placeholders]\n*/\n/*!------------------------------------------------------------------\n[Contrast for backgrounds. This should be opposite of text-primary-color]\n*/\n/*!------------------------------------------------------------------\n[Input boxes, text areas, etc. All .form-control]\n*/\n/*!---------------------------------